import Swiper from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import { Navigation, Pagination, Keyboard } from "swiper/modules";

new Swiper(".feedback-swiper", {
  modules: [Navigation, Pagination, Keyboard],
  pagination: {
    el: ".swiper-pagination",
    clickable: true,
  },
  navigation: {
    nextEl: ".swiper-button-nex",
    prevEl: ".swiper-button-pre",
  },
  keyboard: {
    enabled: true,         // –£–≤—ñ–º–∫–Ω—É—Ç–∏ –∫–µ—Ä—É–≤–∞–Ω–Ω—è –∑ –∫–ª–∞–≤—ñ–∞—Ç—É—Ä–∏
    onlyInViewport: true,  // –ü—Ä–∞—Ü—é—î –ª–∏—à–µ –∫–æ–ª–∏ —Å–ª–∞–π–¥–µ—Ä —É –ø–æ–ª—ñ –∑–æ—Ä—É
  },
  // autoHeight: false,       // –∑–º—É—à—É—î –≤—Å—ñ —Å–ª–∞–π–¥–∏ –º–∞—Ç–∏ –æ–¥–Ω–∞–∫–æ–≤—É –≤–∏—Å–æ—Ç—É (–Ω–∞–π–≤–∏—â—É —Å–µ—Ä–µ–¥ –Ω–∏—Ö).
  grabCursor: true,
  breakpoints: {
    375: {
      slidesPerView: 1,
      spaceBetween: 20,
    },
    768: {
      slidesPerView: 2,
      spaceBetween: 24,
    },
    1440: {
      slidesPerView: 3,
      spaceBetween: 24,
    },
  },
});